* {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

html,
body,
#root {
  min-height: 100vh;
}

h1 {
  height: 100%;
  font-size: 30px;
}

textarea,
input {
  padding: 2px;
  outline: none;
  color: black;
}

main {
  min-height: 100vh;
}

/*  */
.articleCard {
  /* min-width: calc(100% - 40px); */
  max-width: 250px;
  background-color: #b3b8c9;
  padding: 30px 20px;
  margin: 10px 0;
}

/*  */
.loader {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader > div {
  position: absolute;
  border: 4px solid rgb(5, 5, 5);
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}

.loader > div:nth-child(2) {
  animation-delay: -0.5s;
}

@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 0;
  }
  5% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}

mark {
  background: orange;
  color: black;
}
